import timeLine from '@/components/timeLine.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
export default {
	components: {
		timeLine,
		uniIcon
	},
	data() {
		return {
			orderList: '',
			orderType:1,// 1 预约订单 2 待付款订单
		};
	},
	onLoad(option) {
		if(option.orderType){
			this.orderType = option.orderType;
			console.log(this.orderType)
			this.setNavBar();
		}
		this.queryOrder();
	},

	methods: {
		queryOrder() {
			let _this = this;
			this.$http.request({
				url: this.$api.queryMyOrder,
				data: {
					orderStatusType: _this.orderType
				},
				method: 'POST'
			}).then(res => {
				let data = res.data;
				this.orderList = data;
			})
		},
		goDetail(id) {
			uni.navigateTo({
				url: '../reservarOrderDetail/reservarOrderDetail?orderId=' + id+'&orderType='+this.orderType
			})
		},
		setNavBar(){
			let title = "待付款";
			if(this.orderType == '3'){
				title = '待安装/待维修'
			}
			uni.setNavigationBarTitle({
				title:title
			})
		},
		deleOrder(id){
			let _this = this;
			uni.showModal({
				title:"提示",
				content:"确认删除此订单吗?",
				success(res){
					if(res.confirm){
						_this.$http.request({
							url:_this.$api.Cancel_Order,
							data:{
								orderId:id
							},
							method:'POST'
						}).then(res => {
							if(res.errno == '0'){
								_this.queryOrder();
							}								
						})
					}
				}	
			})
			
		},
		evaluate(orderId){
			console.log('评价评价')
			uni.navigateTo({
				url:'../makeComment/makeComment?orderId='+orderId
			})
		}
	}
}
